import React from 'react'
import Platopaceno from '../../assets/img/platopaceno.jpg'
export const Platopaceo = () => {
  return (
    <div class="container text-center">
    <h3>Este es el Plato Paceo</h3>
        <div class="row align-items-center">
            <div class="col">
            <img src={Platopaceno} alt='' className='imgplp'></img>

            </div>
            <div class="col">
            <p><strong>El Plato Paceo es un platillo tradicional muy representativo de La Paz</strong>, Bolivia. Se compone principalmente de choclo (maz)
            , habas, papas cocidas con cscara y queso frito1. Es comn acompaarlo con llajua, una salsa picante tpica boliviana.
            La historia del Plato Paceo se remonta al cerco de La Paz en 1781, liderado por Tpac Katari y Bartolina Sisa. Durante este perodo,
            los alimentos escaseaban y los pobladores necesitaban una comida que pudiera prepararse con ingredientes locales disponibles. 
            Con el tiempo, se aadi carne al plato, aunque esta adicin no es aceptada por todos.
            Hoy en da, el Plato Paceo se disfruta especialmente durante festividades como el 16 de Julio y el 20 de Octubre, as como en la 
            festividad de Alasita cada 24 de Enero. Es un plato que celebra la rica cultura culinaria y las tradiciones de La Paz, y es un 
            ejemplo de la diversidad de ingredientes que ofrece la regin andina.</p>
            </div>
        </div>
    </div>
  )
}
